.brands-page {
  .content {
    margin-top: 30px;
    gap: 50px;
    flex-wrap: wrap;
  }
  .brand-card {
    width: 378px;
    height: 283px;
    border-radius: 13px;
    background-color: #ffffff;
    cursor: pointer;
    text-align: center;
    position: relative;

    .card-content {
      padding: 20px;
      box-sizing: border-box;
      height: 15em;
      display: flex;
      justify-content: space-between;
      flex-direction: column;

      img {
        height: 97px;
        margin: auto;
      }
    }

    .footer {
      position: absolute;
      bottom: 0;
      background-color: #9999ff;
      padding: 20px;
      box-sizing: border-box;
      width: 100%;
      height: 45px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom-left-radius: 13px;
      border-bottom-right-radius: 13px;

      p {
        color: #ffffff;
      }
    }
  }
}
